@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    width: 100%;
    background-size: cover;    
    background-repeat: no-repeat;
    background-image: url('../img/header.jpg');
    background-position: center center;
    position: relative;
    height: 50vh;

    &.inicio{
        background-position: 65% 100%;
        background-image: url('../img/mainheader.jpg');
        height: 50vh;
        @include m.tablet{
          height: 100vh;
          background-position: bottom right;
        }
        .menu-bar{
            background-color: rgb(0,0,0,0.6);
            @include m.tablet{
              background-color: transparent;
            }
        }
    }

    .menu-bar{
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 1rem;
        position: absolute;
        background-color: rgb(0,0,0,0.5);
        font-family: v.$fuenteTitle;

        @include m.tablet{
          display: flex;
          flex-direction: row;
          justify-content: space-around;
          padding-top: 1rem;
          background-color: rgb(0,0,0,0.4);
          box-shadow: 0 0px 10px black;
          font-family: v.$fuenteTitle;
          visibility: visible;
          opacity: 1;
          height: auto;
        }

        a{
            color: v.$blanco;
            font-size: 2.6rem;
            text-transform: uppercase;
            text-shadow: 3px 3px 5px black;
            text-align: center;
            width: 100%;
            filter: blur(1.5px);
            &:hover{
                filter: none;
                background-color: v.$negro;
                @include m.tablet{
                  background-color: transparent;
                }
            }
            @include m.phone{
              font-size: 3.4rem;
            }
            @include m.tablet{
              font-size: 4.2rem;
              filter: blur(3px);
              width: auto;
            }
            @include m.desktop{
              font-size: 5rem;
            }
            @include m.bigscreen{
              font-size: 5.5rem;
            }
        }          
    }
}

.mobile-menu{
  padding-top: 2rem;
  background-color: rgb(0,0,0,0.5);
  @include m.tablet{
      display: none;
  }

  img{
      width: 3.5rem;
      margin: auto;      
      cursor: pointer;
  }
}
.mostrar{
  display: none !important;
  @include m.tablet{
    display: flex !important;
  }
}

.index-title{
    display: flex;
    flex-direction: column;
    height: 40vh;
    justify-content: flex-end;
    @include m.tablet{
      height: 95vh;
    }
        h1{
            font-family: v.$fuenteLed;
            color: #ee00f9;
            font-size:5rem;
            line-height: 1.2;
            padding-left: 1rem;
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            animation: pruebas 2s linear infinite;

            @include m.mini{
              font-size: 7rem;
            }
            
            @include m.phone{
              font-size: 9rem;
              padding-left: 2rem;
            }
            @include m.tablet{
              text-shadow: -4px -4px 2px v.$azul;
              padding-left: 4rem;
              font-size: 15rem;
            }
            @include m.desktop{
              text-shadow: -4px -4px 3px v.$azul;
            }
            @include m.bigscreen{
              text-shadow: none;
              font-size: 17rem;
            }
        }
}

@keyframes pruebas{
    0%{
     background-image: linear-gradient(to bottom, white 0%, #ee00f9 10%);
    }
    10%{
      background-image: linear-gradient(to bottom, white 15%, #ee00f9 25%);
    }
    20%{
      background-image: linear-gradient(to top, white 20%, #ee00f9 30%);
    }
    30%{
       background-image: linear-gradient(to top, white 25%, #ee00f9 35%);
    }
    40%{
      background-image: linear-gradient(to bottom,#ee00f9 20%, white 40%, #ee00f9 50%);
    }
    50%{
       background-image: linear-gradient(to bottom,#ee00f9 25%, white 55%, #ee00f9 65%);
    }
    60%{
      background-image: linear-gradient(to top, white 20%, #ee00f9 30%);
    }
    70%{
       background-image: linear-gradient(to top, white 30%, #ee00f9 40%);
    }
    80%{
      background-image: linear-gradient(to bottom, white 10%, #ee00f9 20%);
    }
    90%{
      background-image: linear-gradient(to bottom,#ee00f9 10%, white 55%, #ee00f9 65%);
   }
   100%{
     background-image: linear-gradient(to top, white 20%, #ee00f9 30%);
   }
  }

  .section-title{
    display: grid;
    place-items: center;
    height: 40vh;
      h1{
        text-transform: uppercase;
        color: v.$fucsia;
        text-shadow: 3px 3px 5px #fff;
        font-size: 5rem;
        @include m.phone{
          font-size: 7rem;
        }
        @include m.tablet{
          font-size: 12rem;
        }
        @include m.desktop{
          padding-top: 12rem;
          font-size: 15rem;
        }
        @include m.bigscreen{
          font-size: 18rem;
        }
        @include m.giant{
          padding-top: 20rem;
          font-size: 28rem;
        }
      }
  }